import { useEffect, useState, useRef } from 'react';
import {Layer, Stage} from 'react-konva';
import { Contracts } from './useContracts';
import { Recon } from './useRecon';

// import { useCanvas } from './useCanvas';
import axios from 'axios';
import _ from 'lodash';

import warzone from './map-overview-bg.jpg';
import './App.css';

const { REACT_APP_API_URL } = process.env;

const api = axios.create({
  baseURL: REACT_APP_API_URL,
});



function App() {
  const menuRef = useRef(null);

  const [ coordinate, setCoordinate ] = useState([]);
  const [ coordinates, setCoordinates ] = useState([]);

  const [ contract, setContract ] = useState({});
  const [ addRecon, setAddRecon ] = useState(false);
  const [ createContract, setCreateContract ] = useState(false);

  const canvasWidth = window.innerWidth;
  const canvasHeight = window.innerHeight;

  // const [ menuRef, coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight, setStart, clickedContract ] = useCanvas();
  // const [ currentCoord, setCurrentCoord ] = useState({})
  // const [ contractCoord, setContractCoord ] = useState({})
  // const [ reconCoord, setReconCord ] = useState(false)
  
  const removeAllCorrdinates = async () => {
    let iterations = coordinates.length;

    for (const coordinate of coordinates) {
      if(coordinate.id){
        await api.delete(`${coordinate.id}`);
      }

      if (!--iterations)
        setCoordinates([]);
    }
  }


  const drawMenu = (location) => {
        const menu = menuRef.current;
        let locationX = (canvasWidth * location.x) / 100;
        let locationY = (canvasHeight * location.y) / 100;
        menu.style.left = `${locationX}px`;
        menu.style.top = `${locationY}px`;
  }


  const handleCanvasClick = (event) => {
    event = (event.evt) ? event.evt : event;

    let position = {}
    position.x = (100 / (canvasWidth / event.clientX));
    position.y = 100 / (canvasHeight / event.clientY);
    let _coordinate = {
      x: position.x, 
      y: position.y,
      type: null
    };

    if(addRecon && !_.isEmpty(contract) && contract.id){
      _coordinate.type = 'recon';
      _coordinate.parentId = contract.id;
    }
    if(event.createContract){
      _coordinate.type = 'contract';
      // create contract
    }

      // open menu
    drawMenu(position)
    
    // setContract({});
    // console.log(coordinate)
    // console.log('click');
    // if(coordinate.type){
    //   console.log(coordinate)
    // }

    setCoordinate(_coordinate)
  }

  const doCreateContract = (event) =>{
    setCreateContract(true);
    let position = {
      x: (100 / (canvasWidth / event.clientX)),
      y: (100 / (canvasHeight / event.clientY)),
      type: 'contract'
    }
    api.post("",{ ...coordinate })
    .then(res => {
      setCoordinate(res.data)
      setContract(res.data)
      setCoordinates([...coordinates, res.data]);
    })
  }

  const doAddRecon = (event) =>{
    setAddRecon(true);
    let position = {
      parentId: contract.id,
      x: (100 / (canvasWidth / event.clientX)),
      y: (100 / (canvasHeight / event.clientY)),
      type: 'recon'
    }
    api.post("",{ ...coordinate })
    .then(res => {
      setCoordinate(res.data)
      setContract({})
      setCoordinates([...coordinates, res.data]);
    })
  };

  const doRemove = () =>{
    // const _coordinates = coordinates.filter((obj) => {
    //   return obj.x !== currentCoord.x && obj.y !== currentCoord.y
    // })
    // if(currentCoord.type === 'recon'){
    //   setReconCord(false);
    // }

    // setCoordinates([...coordinates]);
  }

  // useEffect(()=>{
  //   console.log(coordinate);
  //   if(coordinate.type){
  //     const coordinateCheck = coordinates.find(x => x.id === coordinate.id);
  //     console.log(coordinateCheck)
  //     if(!coordinateCheck){
  //       api.post("",{ ...coordinate })
  //       .then(res => {
  //         setCoordinate(res.data)
  //         setCoordinates([...coordinates, res.data]);
  //       })
  //     }
  //   }
  // },[coordinate, coordinates])

  useEffect(() =>{
    console.log(contract)
    console.log(coordinates.filter(x => x.type === 'contract' && x.id === contract.id))
  },[contract, coordinates])

  useEffect(() =>{
    api.get()
      .then(res => {
        const _coordinates = res.data;
        console.log(_coordinates)
        setCoordinates(_coordinates);
      })
  },[])
 
  return (
    <main id="container" className="App-main" >
        <img  src={warzone} className="img-warzone" alt="logo" />
        <Stage 
          width={canvasWidth} 
          height={canvasHeight}
          onClick={handleCanvasClick}
          >
          <Layer>
          {
            _.isEmpty(contract) ?
            <Contracts coordinates={coordinates.filter(x => x.type === 'contract')} setContract={setContract}/>
            :
            <Contracts coordinates={coordinates.filter(x => x.type === 'contract' && x.id === contract.id)} setContract={setContract}/>
          }
          <Recon coordinates={coordinates.filter(x => x.type === 'recons' && x.parentId === contract.id)} />
          </Layer>
        </Stage>

  
      <div ref={menuRef} id="contextMenu" >
          { 
            !createContract &&
            <div className="contextMenu--contract" onClick={doCreateContract}>Create Contract</div>
          }
          { 
            !addRecon &&
            <div className="contextMenu--recon" onClick={doAddRecon}>Add Recon</div>
          }
          {

            <div className="contextMenu--remove" >Remove</div>
          }
      </div>

      
      <div className="clearButton" style={{zIndex:1000}}>
        <button onClick={removeAllCorrdinates}> Clear Map </button>
      </div> 
    </main>
  );

};

export default App;